
  .wrapper {
    border-radius: 10px;
    border: 1px solid #ccc;
  }
  
  .text-input {
    display: flex;
    border-bottom: 1px solid #ccc;
  }
  
  .to-text {
    border-radius: 0px 10px 10px 0px;
    border-left: 1px solid #ccc;
  }
  
  textarea {
    height: 300px;
    border: none;
    outline: none;
    width: 100%;
    border-radius: 10px;
    resize: none;
    padding: 10px;
    font-size: 18px;
  }
  
  textarea::placeholder {
    color: #b7b7b7;
  }
  
  .controls, li, .icons, .icons i {
    display: flex;    
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  
  .controls .row .icons i {
    width: 40px;
    font-size: 16px;
    color: #b7b7b7;
    cursor: pointer;
    justify-content: center;
  }
  
  .controls .row.from .icons {
    padding-right: 15px;
    border-right: 1px solid #ccc;
  }
  
  .controls .row.to .icons {
    padding-left: 15px;
    border-left: 1px solid #ccc;
  }
  
  select {
    color: #333;
    border: none;
    outline: none;
    font-size: 18px;
    padding: 10px;
    margin: 0px 20px 0px 15px
  }
  
  button {
    width: 100%;
    padding: 15px;
    border: none;
    color: #fff;
    cursor: pointer;
    margin-top: 20px;
    border-radius: 5px;
    font-size: 18px;
    background: #600ee4;
  }